<template>
  <el-card class="box-card">
    <el-form ref="dataForm" :model="param" :rules="rule" label-width="160px">
      <el-form-item label="username" prop="username">
        <el-input v-model="param.username" placeholder="username"></el-input>
      </el-form-item>
      <el-form-item label="firstName" prop="firstName">
        <el-input v-model="param.firstName" placeholder="firstName"></el-input>
      </el-form-item>
      <el-form-item label="lastName" prop="lastName">
        <el-input v-model="param.lastName" placeholder="lastName"></el-input>
      </el-form-item>
      <el-form-item label="address" prop="address">
        <el-input v-model="param.address" placeholder="address"></el-input>
      </el-form-item>
      <el-form-item label="birthdate" prop="birthdate">
        <el-date-picker  value-format="yyyy-MM-dd"  v-model="param.birthdate" placeholder="birthdate"></el-date-picker>
      </el-form-item>
      <el-form-item label="taxid" prop="taxid">
        <el-input v-model="param.taxid" placeholder="taxid"></el-input>
      </el-form-item>
      <el-form-item label="hired" prop="hired">
        <el-date-picker  value-format="yyyy-MM-dd"  v-model="param.hired" placeholder="hired"></el-date-picker>
      </el-form-item>
      <el-form-item label="employeeExperience" prop="employeeExperience">
        <el-input type="number" v-model="param.employeeExperience" placeholder="employeeExperience"></el-input>
      </el-form-item>
      <el-form-item label="salary" prop="salary">
        <el-input type="number" v-model="param.salary" placeholder="salary"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">confirm</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  data(){
    return {
      param: {
        username: '',
        firstName: '',
        lastName: '',
        address: '',
        birthdate: '',
        taxid: '',
        hired: '',
        employeeExperience: '',
        salary: '',
      },
      rule: {
        username: [{required: true, message: "please fill in username", trigger: 'blur'}],
        firstName: [{required: true, message: "please fill in firstName", trigger: 'blur'}],
        lastName: [{required: true, message: "please fill in lastName", trigger: 'blur'}],
        address: [{required: true, message: "please fill in address", trigger: 'blur'}],
        birthdate: [{required: true, message: "please fill in birthdate", trigger: 'blur'}],
        taxid: [{required: true, message: "please fill in taxid", trigger: 'blur'}],
        hired: [{required: true, message: "please fill in hired", trigger: 'blur'}],
        employeeExperience: [{required: true, message: "please fill in employeeExperience", trigger: 'blur'}],
        salary: [{required: true, message: "please fill in salary", trigger: 'blur'}],
      }
    }
  },
  methods: {
    onSubmit() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$axios.post('/employees/create', this.param).then(res => {
            if (res.success) {
              this.$message({
                message: 'successfully added',
                type: 'success'
              });
              this.$router.back();
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    }
  }
}
</script>

